import React, { Component } from 'react';
import anime from 'animejs'

export default class AddUserBox extends Component {
  componentDidUpdate = (prevProps) => {
    console.log(this.props.addUserErrorMessage, prevProps.addUserErrorMessage)
    if (this.props.addUserErrorMessage !== prevProps.addUserErrorMessage && this.props.addUserErrorMessage !== "") {
      if (this.animation)
        this.animation.restart()
      this.animation = anime({
        targets: '.error-message',
        color: "#8B0000",
        easing: 'linear',
        duration: 350,
        "font-size": "12px"
      });
    }
  }

  render() {
    return (
      <div className="user-view">
          <div className="user-commit-info new">
            <h2>Add a new user to this list!</h2>
            <form onSubmit={this.props.addUserToList}>
              <input type="text"
                    placeholder="Github Username"
                    value={this.props.newUserName}
                    onChange={this.props.newUserNameChanged}/>
            </form>
            <div className="error-message">{this.props.addUserErrorMessage}</div>
          </div>
      </div>
    );
  }
}