import React, { Component } from 'react';
import { PropagateLoader } from 'react-spinners';

export default class UserlistView extends Component {
  render() {
    const users = this.props.list.users
    users.sort((name1, name2) => name1.localeCompare(name2))

    let className = "userlist-box"
    if (this.props.selected)
      className += " selected-userlist-box"
    if (this.props.loading)
      className += " loading-userlist-box"
    
    return (
      <div className={className} onClick={() => this.props.requestDataForList(this.props.listID)}>
        <div>
          <div className="userlist-title">
            {this.props.list.title}
          </div>
          {users.map(user => <div className="userlist-member">{user}</div>)}
        </div>
        <div className="loader-container">
          <PropagateLoader
            style={{
              display: "block",
              margin: "0 auto",
              "border-color": "red"}}
            sizeUnit={"px"}
            size={8}
            color={'#C6F8FF'}
            loading={this.props.loading}
          />
        </div>
      </div>
    )
  }
}