import React, { Component } from 'react';
import Conversation from './Conversation';
import anime from 'animejs'

export default class UserView extends Component {
  componentDidMount = () => {
    anime({
      targets: '.user-view',
      translateY: 250,
      duration: 1200
    });
  }
  
  render() {
    const {name, date, repo, repoID, commitMessage, commitURL} = this.props.user
    let className = "user-commit-info"
    if (this.props.ownedByUser)
      className += " owned-by-user"
    
    return (
      <div className="user-view">
        <a href={commitURL}>
          <div className={className}>
            <h1>{this.prettifyRepoName(repo)}</h1>
            <p>{name} ({date.toLocaleString()})</p>
            <p>"{commitMessage}"</p>
          </div>
        </a>

        <Conversation repoID={repoID}
                      commitURL={commitURL}
                      messages={this.props.messages}
                      commentTextChanged={this.props.commentTextChanged}
                      commentText={this.props.commentBeingWritten}
                      addComment={this.props.addComment}
                      canMakeComments={this.props.canMakeComments}/>
      </div>
    );
  }

  prettifyRepoName = (string) => {
    string = string.split("/")[1]
    string = string.replace(/-/g, ' ');
    string = this.capitalize(string)
    string = this.separateCamelCase(string)
    return string
  }

  capitalize = (string) => {
    return string.split(" ").map(word => word[0].toUpperCase() + word.slice(1)).join(" ")
  }

  separateCamelCase = (string) => {
    let chars = string.split("")
    chars = chars.map((char, index) => index !== 0 && char.toUpperCase() === char && char.toLowerCase() !== char
                      ? " " + char
                      : char)
    return chars
  }
}