import React, { Component } from 'react';
import UserList from './UserList';
import Sidebar from './Sidebar';
import LoginToGithubButton from './LoginToGithubButton';
import anime from 'animejs'

export default class MainView extends Component {
  state = {
    loggingIn: false,
    newListName: "",
    displayedList: null,
    commentsBeingWritten: {},
    activeConversations: [],
    newUserName: "",
    loadingUserList: false
  }

  componentDidMount = () => {
    this.setupSocket()
    this.authenticateWithAccessToken()
  }

  authenticateWithAccessToken  = () => {
    // localStorage.setItem("githubAccessToken", "help") 
    const accessToken = localStorage.getItem("githubAccessToken")
    if (accessToken !== null) {
      this.setState({loggingIn: true})
      this.socket.emit("authWithGithub", {token: accessToken})
    }
  }

  authenticateWithAccessCode = () => {
    // if not already logging in with stored token
    if (!this.state.loggingIn) {
      const url = window.location.href.split("/?code=")
      if (url.length > 1) {
        this.setState({loggingIn: true})
        const accessCode = url[1]
        this.socket.emit("authWithGithub", {code: accessCode})
      }
    }
  }

  setupSocket = () => {
    // const url = "http://localhost:3000"
    // const url = "http://10.185.5.88:3000/"
    // const url = "https://ddf9867e.ngrok.io"
    const socket = require('socket.io-client')();
    socket.on('commentAdded', this.receiveComment);
    socket.on("authenticationSuccess", this.loginSuccess)
    socket.on("authenticationFailure", this.loginFailure)
    socket.on("userDataForDisplay", this.displayUserList)
    socket.on("userAddedToList", this.userAddedToList)
    socket.on("addUserToListResponse", this.addUserToListResponse)
    socket.on("dataForUserlistResponse", this.addVisibleUserList)
    socket.on("createdNewList", this.createdNewList)
    this.socket = socket
    socket.on("connect", this.authenticateWithAccessCode)
  }

  loginSuccess = (data) => {
    this.setState({loggingIn: false})
    
    console.log("Logged in and got token back!", data.loginData.accessToken)
    localStorage.setItem("githubAccessToken", data.loginData.accessToken)
    this.setState({
      loginData: data.loginData,
    })
    this.setInitialListsState(data.userlistsData)
  }

  loginFailure = () => {
    this.setState({loggingIn: false})
    console.log("Failed to login")
    // remove existing token in case it was expired
    localStorage.removeItem("githubAccessToken")
  }

  setInitialReposState = (data) => {
    this.setState({
      activeConversations: data.conversations,
      users: data.users.map(this.shapeDataForUser)
      .sort((user1, user2) => user2.date.getTime() - user1.date.getTime())
    })
  }

  setInitialListsState = (userlists) => {
    this.setState({
      visibleUserLists: userlists
    })
  }

  shapeDataForUser = (user) => {
    return {
      name: user.name,
      repo: user.data.repo.name,
      repoID: user.data.repo.id,
      commitID: user.data.payload.push_id,
      commitMessage: user.data.payload.commits[0].message,
      commitURL: user.data.payload.commits[0].url.replace("api.", "").replace("/repos", "").replace("commits", "commit"),
      date: new Date(user.data.created_at)
    }
  }

  requestDataForList = (listID) => {
    this.setState({displayedList: listID, loadingUserList: true})
    this.socket.emit("requestReposForList", {listID, accessToken: this.state.loginData.accessToken})

    const intoSpaceAnimation = anime({
      targets: '.user-view',
      translateY: -200,
      duration: 1200,
      complete: (anim) => {
        // intoSpaceAnimation.finished.then(this.setState({users: []}))
        this.setState({users: [], activeConversations: []})
      }
    });
  }

  displayUserList = ({userData, conversations}) => {
    console.log(userData)
    this.setState({
      users: userData.map(this.shapeDataForUser)
      .sort((user1, user2) => user2.date.getTime() - user1.date.getTime()),
      newUserName: "",
      addUserErrorMessage: "",
      loadingUserList: false,
      activeConversations: conversations
    })
  }

  receiveComment = (data) => {
    const conversations = [...this.state.activeConversations]

    let conversation = conversations.find(conversation => parseInt(conversation.repoID) === data.repoID)
    if (conversation !== undefined) {
      conversation = {...conversation}
    } else {
      conversation = {messages: [], repoID: String(data.repoID)}
      conversations.push(conversation)
    }
    conversation.messages.push({message: data.commentContent, sender: data.username})
    
    this.setState({activeConversations: conversations})
  }

  userAddedToList = ({listID, username}) => {
    // if we are added to a list, giving us visibility of it
    if (username === this.state.loginData.basicUserData.login) {
      this.socket.emit("requestDataForUserlist", {listID})
    }
    
    // if a user is added to a list we have visibility of
    const list = this.state.visibleUserLists.find(visibleList => visibleList.id === listID)
    if (list !== undefined) {
      const users = [...list.users, username]
      const newVisibleUserLists = [...this.state.visibleUserLists]
      newVisibleUserLists[newVisibleUserLists.indexOf(list)].users = users
      this.setState({visibleUserLists: newVisibleUserLists})
      // fetch data for the new user if we're currently displaying it
      if (listID === this.state.displayedList) {
        this.requestDataForList(listID)
      }
    }
  }

  addUserToListResponse = ({message}) => {
    this.setState({addUserErrorMessage: message})
  }

  addVisibleUserList = ({listData}) => {
    this.setState({visibleUserLists: [...this.state.visibleUserLists, listData]})
  }

  createdNewList = ({listData}) => {
    this.setState({visibleUserLists: [...this.state.visibleUserLists, listData]})
  }

  render() {
    if (this.getLoggedInUser() !== null)
      return (
        <div className="main-view">
          <Sidebar loggingIn={this.state.loggingIn}
                    loggedInUser={this.getLoggedInUser()}
                    createNewList={this.createNewList}
                    newListName={this.state.newListName}
                    newListNameChanged={this.newListNameChanged}
                    visibleUserLists={this.state.visibleUserLists}
                    requestDataForList={this.requestDataForList}
                    displayedList={this.state.displayedList}
                    loadingList={this.state.loadingUserList ? this.state.displayedList : null}/>
          {
            this.state.loginData ? (
              this.state && /*this.state.activeConversations &&*/ this.state.users ? 
                <UserList users={this.state.users}
                        activeConversations={this.state.activeConversations}

                        commentsBeingWritten={this.state.commentsBeingWritten}
                        commentTextChanged={this.commentTextChanged}
                        addComment={this.addComment}
                        loggedInUser={this.state.loginData ? this.state.loginData.basicUserData.login : null}

                        newUserName={this.state.newUserName}
                        newUserNameChanged={this.newUserNameChanged}
                        addUserToList={this.addUserToList}
                        addUserErrorMessage={this.state.addUserErrorMessage}/> 
                : <h2>Select a list of users to display!</h2>
            ) : null
          }
        </div>
      );
    else
      return <LoginToGithubButton/>
  }

  createNewList = () => {
    this.socket.emit("createNewList", {name: this.state.newListName, username: this.state.loginData.basicUserData.login})
    console.log("Making new list with name " + this.state.newListName)
    this.setState({newListName: ""})
  }

  newListNameChanged = (event) => {
    this.setState({newListName: event.target.value})
  }
  
  getLoggedInUser = () => {
    return this.state.loginData !== undefined ? this.state.loginData.basicUserData.login : null
  }

  commentTextChanged = (event, repoID) => {
    const comments = {...this.state.commentsBeingWritten}
    comments[repoID] = event.target.value
    this.setState({commentsBeingWritten: comments})
  }

  addComment = async (event, repoID, commitURL) => {
    event.preventDefault()

    this.socket.emit("addComment", {
                                    repoID: repoID,
                                    commitURL: commitURL,
                                    commentContent: this.state.commentsBeingWritten[repoID],
                                    topic: this.state.users.find(user => user.repoID === repoID).repo,
                                    username: this.state.loginData.basicUserData.login,
                                    accessToken: this.state.loginData.accessToken
                                  })
    
    const commentsBeingWritten = {...this.state.commentsBeingWritten}
    delete commentsBeingWritten[repoID]
    this.setState({commentsBeingWritten: commentsBeingWritten})
  }

  newUserNameChanged = (event) => {
    this.setState({newUserName: event.target.value})
  }

  addUserToList = (event) => {
    event.preventDefault()
    this.setState({newUserName: "", addUserErrorMessage: ""})
    this.socket.emit("addUserToList", {
      listID: this.state.displayedList,
      username: this.state.newUserName,
      accessToken: this.state.loginData.accessToken
    })
  }
}