import React, { Component } from 'react';
import UserlistView from './UserlistView';
import AddUserListBox from './AddUserListBox';

export default class Sidebar extends Component {
  render() {
    return (
      <div className="sidebar">
        <span className="username">
        {
          !this.props.loggingIn
          ? `Logged in as ${this.props.loggedInUser}`
          : `Logging in...`
        }
        </span>
        <div>
          {this.props.visibleUserLists ?
            this.props.visibleUserLists.map(list => 
              <UserlistView list={list}
                            key={list.id}
                            listID={list.id}
                            requestDataForList={this.props.requestDataForList}
                            selected={this.props.displayedList === list.id}
                            loading={this.props.loadingList === list.id}/>
            )
            : null}
          <AddUserListBox newListName={this.props.newListName} newListNameChanged={this.props.newListNameChanged} createNewList={this.props.createNewList}/>
        </div>
      </div>
    );
  }
}